@import "_import";

.map {
  min-height: calc(100vh - 280rem);
  background-color: #fff;
  padding: 190rem 0 214rem;
  @include full {
    padding: 190px 0 214px;
  }
  @include mobile {
    padding: 122rem 15rem 56rem;
  }
  &-wrap {
    max-width: 1600rem;
    margin: 0 auto;
    @include full {
      max-width: 1600px;
    }
    @include mobile {
      max-width: none;
    }
  }
  &__mtit {
    margin: 0 0 46rem;
    font-family: "ambit-semi";
    font-size: 82rem;
    text-align: center;
    @include full {
      margin: 0 0 46px;
      font-size: 82px;
    }
    @include mobile {
      font-size: 40rem;
      margin: 0 0 14rem;
      text-align: left;
      text-indent: -3rem;
      letter-spacing: -1.1rem;
    }
  }
  &__img {
    width: 800rem;
    @include full {
      width: 800px;
    }
    @include mobile {
      width: 100%;
    }
  }
  &__sec {
    width: 760rem;
    @include full {
      width: 760px;
    }
    @include mobile {
      width: 100%;
    }
  }
  &__info {
    border-top: 1px solid  #000;
    &--1 {
      padding: 22rem 0 40rem 0;
      @include full {
        padding: 22px 0 40px 0;
      }
      @include mobile {
        padding: 30rem 0 24rem 0;
        border-top: none;
      }
    }
    &--2 {
      padding: 24rem 0 86rem 0;
      @include full {
        padding: 24px 0 86px 0;
      }
      @include mobile {
        padding: 16rem 0 50rem 0;
      }
    }
    &--3 {
      padding: 24rem 0 0 0;
      @include full {
        padding: 24px 0 0 0;
      }
      @include mobile {
        padding: 15rem 0 0 0;
      }
    }
  }
  &__tit {
    font-family: "Noto Sans KR Medium";
    font-size: 18rem;
    line-height: 1.78;
    letter-spacing: -1rem;
    @include full {
      font-size: 18px;
      letter-spacing: -1rem;
    }
    @include pc {
      font-size: 21rem;
    }
    @include mobile {
      font-size: 12rem;
      line-height: 1.7;
    }
  }
  &__txt {
    width: 380rem;
    @include full {
      width: 380px;
    }
    @include mobile {
        width: 200rem;
    }
  }
  &__txt-main {
    display: block;
    margin: 0 0 14rem 0;
    font-family: "Noto Sans KR Medium";
    // font-size: 48rem;
    font-size: 46rem;
    line-height: 1.33;
    letter-spacing: -0.4rem;
    @include full {
      margin: 0 0 14px 0;
      font-size: 46px;
      letter-spacing: -0.4px;
    }
    @include mobile {
      margin: 0 0 9rem 0;
      font-size: 24rem;
      line-height: 1.33;
      letter-spacing: -1rem;
    }
  }
  &__txt-sub {
    margin: 0 0 160rem 0;
    font-family: "Noto Sans KR Medium";
    font-size: 18rem;
    line-height: 1.8;
    letter-spacing: -0.6rem;
    @include full {
      margin: 0 0 160px 0;
      font-size: 18px;
      letter-spacing: -0.6px;
    }
    @include mobile {
      margin: 0 0 80rem 0;
      font-size: 12rem;
    };
  }
  &__tel {
    font-family: "ambit-semi";
    font-size: 48rem;
    letter-spacing: -0.5rem;
    @include full {
      font-size: 48px;
      letter-spacing: -0.5px;
    }
    @include mobile {
      font-size: 24rem;
      letter-spacing: -0.3rem;
    }
  }
  &__hours-itm {
    display: flex;
    margin: 2rem 0 0 0;
    &:first-child {
      margin: 0;
    }
    @include mobile {
      margin: 0;
    }
  }
  &__hours-day {
    width: 120rem;
    margin: 0 68rem 0 0;
    font-family: "Noto Sans KR Medium";
    font-size: 20rem;
    line-height: 1.6;
    letter-spacing: -0.7rem;
    @include full {
      width: 120px;
      margin: 0 68px 0 0;
      font-size: 20px;
      letter-spacing: -0.7px;
    }
    @include mobile {
      width: 98rem;
      margin: 0;
      font-size: 14rem;
      letter-spacing: -0.7px;
    }
  }
  &__hours-time {
    font-family: "ambit-semi";
    font-size: 20rem;
    line-height: 1.6;
    @include full {
      font-size: 20px;
    }
    @include mobile {
      font-size: 14rem;
    }
  }
}